import React from 'react';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import part1 from './Image/22_2_11.jpg';
import part2 from './Image/2_66.jpg';
import part3 from './Image/4_37_4.jpg';
import part4 from './Image/5_1_1.jpg';
import part5 from './Image/7_8.jpg';
import part6 from './Image/2_66.jpg';




const Owl2 = () =>{
    const options = {
        items:1,
        loop:true,
        autoplayTimeout:4000,
        animateOut:'slidOutUp',
        nav:false,
        dots:false,
        responsive:{
          1200:{
            items:4,
          },
          768:{
            items:2,
          },
          320:{
            items:1,
            innerWidth:"100%",
            outerWidth:"100%"
          },
        },
      }
    return(
        <>
        <OwlCarousel className='owl-theme' {...options}>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part1} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">Mangosteen Organic From VietNamese</h5>
                                 <p class="card-text text-danger">&#x24; 550.00 <del className="text-muted">&#x24; 600.00</del></p>
                               {/* </div> */}
        </div>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part2} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">Soda Sparkling Water Maker (Rose Gold)</h5>
                                 <p class="card-text text-danger">&#x24; 500.00</p>
                               {/* </div> */}
        </div>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part3} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">HOT - Lettuce Fresh Produce Fruit</h5>
                                 <p class="card-text text-danger">&#x24; 500.00</p>
                               {/* </div> */}
        </div>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part4} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">Soda Sparkling Water Maker (Rose Gold)</h5>
                                 <p class="card-text text-danger">&#x24; 500.00</p>
                               {/* </div> */}
        </div>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part5} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">Laffy Taffy Laff Bites Gone Bananas</h5>
                                 <p class="card-text text-danger">&#x24; 600.00</p>
                               {/* </div> */}
        </div>
        <div class='item text-center m-3 card shadow p-3 h-100'>
        <img src={part6} class="card-img-top img-fluid" alt="image not found"/>
                               {/* <div class="card-body"> */}
                                 <h5 class="card-title">Ginger Fresh, Whole, Organic - 250gram</h5>
                                 <p class="card-text text-danger">&#x24; 300.00</p>
                               {/* </div> */}
        </div>
        
    </OwlCarousel>
    </>
    );
}
export default Owl2;