import React from "react";
import { Link } from "react-router-dom";
import img1 from './Image/Logo (1).png'
const Nav = () =>{
    return(
      <>
        <nav class="navbar navbar-expand-lg bg-light">
  <div class="container-fluid ">
    <Link class="navbar-brand" to="#"><img src={img1}></img></Link>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse end2 pb-3" id="navbarSupportedContent">
      <ul class="navbar-nav mx-auto mb-2 mb-lg-0">
        <li class="nav-item">
          <Link class="nav-link pe-5" aria-current="page" to="/Home">Home</Link>
        </li>
        
        <li class="nav-item">
          <Link class="nav-link pe-5" aria-current="page" to="/Page">Page</Link>
        </li>
        <li class="nav-item">
          <Link class="nav-link pe-5" to="/Shop">Shop</Link>
        </li>
        <li class="nav-item">
          <Link class="nav-link pe-5" to="/About">About Us</Link>
        </li>
        <li class="nav-item">
          <Link class="nav-link pe-5" to="/Contact">Contact Us</Link>
        </li>
      </ul>
      <i class="bi bi-search m-2 fs-4 text-danger border rounded-circle p-2 effect-icon"></i>
      <i class="bi bi-person m-2 fs-4 text-danger border rounded-circle p-2 effect-icon"></i>
      <i class="bi bi-heart m-2 fs-4 text-danger border rounded-circle p-2 effect-icon"></i>
      <i class="bi bi-bag-heart-fill m-2 fs-4 text-danger border rounded-circle p-2 effect-icon"></i>
    </div>
  </div>
</nav>
</>
    );
}
export default Nav;