import new1 from './Image/icon_1_.png';
import new2 from './Image/icon_2_.png';
import new3 from './Image/icon.png';
import React from "react";
const Imagecard = () =>{
    return(
        <div className="conatiner">
            <div className="row text-center p-5">
            <div className="col-12 col-md-4 pb-5">
                  <img src={new1} className="img-fluid home-card"/>
                    <h6 className='mt-3'>Select Your Products</h6>
                    <small>Choose from select products to start. Keep, add, or remove items.</small>
                </div>
                <div className="col-12 col-md-4 pb-5">
                  <img src={new2} className="img-fluid home-card"/>
                    <h6 className='mt-3'>Select Your Products</h6>
                    <small>Choose from select products to start. Keep, add, or remove items.</small>
                </div>
                <div className="col-12 col-md-4 pb-5">
                  <img src={new3} className="img-fluid home-card"/>
                    <h6 className='mt-3'>Select Your Products</h6>
                    <small>Choose from select products to start. Keep, add, or remove items.</small>
                </div>
            </div>
        </div>
    );
}
export default Imagecard;