import React from "react";
import { Link } from "react-router-dom";
import footer1 from './Image/app-store.png';
import footer2 from './Image/google-play.png';
import footer3 from './Image/payment.png';
const Footer = () =>{
    return(
        <>
        <div className="container-fluid">
            <div className="row text-white footer text-center pt-5 end">
                <div className="col-12 col-md-6 col-lg-3 mb-3">
                    <h6>LOOKING FOR STORE?</h6>
                    <small className="text-muted">78 St. Vicent Place, Glasgow, Greater Newyork NH4014, UK.</small>
                    <h6>DOWNLOAD APP:</h6>
                    <img src={footer1} className="img-fluid d-block mx-auto"></img>
                    <img src={footer2} className="img-fluid d-block mx-auto"></img>
                </div>
                <div className="col-12 col-md-6 col-lg-3 mb-3">
                <h6>NEED HELP?</h6>
                <p className="text-muted">Got Question?</p>
                <Link to="tel:+8819906886" className="d-block text-decoration-none text-white pb-3">+88-1990-6886</Link>
                <small className="d-block text-muted">Monday  Friday<b className="text-white">: 8:10 AM  6:10 PM</b></small>
                <small className="d-block text-muted">Saturday<b className="text-white">: 10:10 AM  06:10 PM</b></small>
                <small className="d-block text-muted">Sunday<b className="text-white">: Close </b></small>
                </div>
                <div className="col-12 col-md-4 col-lg-2 mb-3">
                    <h6>INFORMATION</h6>
                    <ul className="text-muted list-unstyled">
                        <li>Blog Us</li>
                        <li>About Us</li>
                        <li>Secure Shopping</li>
                        <li>Delivery Information</li>
                        <li>Privacy Policy</li>
                        <li>FeedBack</li>
                    </ul>
                </div>
                <div className="col-12 col-md-4 col-lg-2 mb-3">
                    <h6>QUICK LINKS</h6>
                    <ul className="text-muted list-unstyled">
                        <li>Store Location</li>
                        <li>My Account</li>
                        <li>Orders Tracking</li>
                        <li>FAQs</li>
                        
                    </ul>
                </div>
                <div className="col-12 col-md-4 col-lg-2 mb-3">
                    <h6>MY ACCOUNT</h6>
                    <ul className="text-muted list-unstyled">
                        <li>Product Support</li>
                        <li>Checkout</li>
                        <li>Shopping Cart</li>
                        <li>Wishlist</li>
                        <li>Custom Link</li>
                        <li>Redeem Voucher</li>
                    </ul>
                </div>
                <hr className="mt-2"/>
                <div className="col-12 mb-3">
                <img src={footer3} className="img-fluid"></img>
                <small className="d-block text-muted"> Copyright &copy; ORFARM all rights reserved. Powered by <span>Mohsin</span></small>
                </div>
            </div>
        </div>
        </>
    );
}
export default Footer;